	beforeCreate: function () {
	    if(this.$session.exists() && (this.$session.get('user_id')=='' || typeof this.$session.get('user_id')=='undefined')){
			this.$session.destroy();
			this.$router.push('mob001000');
		}else if (!this.$session.exists()) {
			// this.LoginModal= true
			this.$router.push('mob001000')
		}else{
			this.user_agent = String(navigator.userAgent).toLowerCase();
		}
	},
	created() {
		this.setDate();
	},
	mounted() {
		this.updateNow();
		this.fnc_bell();
		setInterval(this.updateNow.bind(this) , 1000);
		setInterval(this.fnc_bell.bind(this) , 15000);

		this.getPartuserModalList('mounted');
		axios.get('/back/SCR005001/Mob001List',{
				params: {
					search: this.user_id
				}
			})
		.then(response => {
			for(var x=0;x<response.data.length;x++){
				if(x==0){
					this.screen_name_001006=response.data[x].clm_screen_name;
					this.screen_authority_001006=response.data[x].screen_authority;
				}else if(x==1){
					this.screen_name_001008=response.data[x].clm_screen_name;
					this.screen_authority_001008=response.data[x].screen_authority;
				}else if(x==2){
					this.screen_name_001001=response.data[x].clm_screen_name;
					this.screen_authority_001001=response.data[x].screen_authority;
				}else if(x==3){
					this.screen_name_001002=response.data[x].clm_screen_name;
					this.screen_authority_001002=response.data[x].screen_authority;
				}else if(x==4){
					this.screen_name_001003=response.data[x].clm_screen_name;
					this.screen_authority_001003=response.data[x].screen_authority;
				}else if(x==5){
					this.screen_name_001004=response.data[x].clm_screen_name;
					this.screen_authority_001004=response.data[x].screen_authority;
				}else if(x==6){
					this.screen_name_001005=response.data[x].clm_screen_name;
					this.screen_authority_001005=response.data[x].screen_authority;
				}else if(x==7){
					this.screen_name_001007=response.data[x].clm_screen_name;
					this.screen_authority_001007=response.data[x].screen_authority;
				}
			}
		})
		.catch(error => {
			console.log(error)
		});

		var date = new Date();
		var year = date.getFullYear();
		var month = (date.getMonth() + 1);
		var day = date.getDate();

		var oneMonthAgo = new Date(date.setMonth(date.getMonth() - 1));
		var oneMonthyear = oneMonthAgo.getFullYear();
		var oneMonthmonth = (oneMonthAgo.getMonth() + 1);
		var oneMonthday = oneMonthAgo.getDate();

		oneMonthmonth = (oneMonthmonth < 10) ? "0" + String(oneMonthmonth) : oneMonthmonth;
		oneMonthday = (oneMonthday < 10) ? "0" + String(oneMonthday) : oneMonthday;

		month = (month < 10) ? "0" + String(month) : month;
		day = (day < 10) ? "0" + String(day) : day;

		// if(Number(day) < 6){
		// 	this.start_date = oneMonthyear + '-' + oneMonthmonth  + '-15';
		// }else{
		// 	this.start_date = year + '-' + month  + '-01';
		// }
		// this.end_date = year + '-' + month  + '-' + day;
		this.getList();

		this.user_document_1=[];
		this.user_document_2=[];
		this.user_document_3=[];
		this.user_document_4=[];
		this.getuserModalList2('mounted');
		var link = this.$router.app.$route.query.from_location;
		var link_kor = '';
		if(link == 'mob001001'){
			link_kor = '생산계획'
		}else if(link == 'mob001002'){
			link_kor = '재단'
		}else if(link == 'mob001003'){
			link_kor = '반제품'
		}else if(link == 'mob001004'){
			link_kor = '조립'
		}else if(link == 'mob001005'){
			link_kor = '완제품'
		}else if(link == 'mob001006'){
			link_kor = '자재입고'
		}else if(link == 'mob001007'){
			link_kor = '출하'
		}else if(link == 'mob001008'){
			link_kor = '자재출고'
		}
		if(String(this.user_agent).indexOf('samsung') > -1 || String(this.user_agent).indexOf('sm-p610') > -1 ){
			ReactNativeWebView.postMessage("MOB001004");
		}
	},
	watch: {
		
	},
	methods: { 
		setDate(){ 
			let year = new Date().getFullYear();
			let month =new Date().getMonth() + 1 < 10? "0" + (new Date().getMonth() + 1): new Date().getMonth() + 1;
			let date =new Date().getDate() < 10? "0" + new Date().getDate(): new Date().getDate();
			let hh =new Date().getHours() < 10? "0" + new Date().getHours(): new Date().getHours();
			let mm =new Date().getMinutes() < 10? "0" + new Date().getMinutes(): new Date().getMinutes();
			let ss =new Date().getSeconds() < 10? "0" + new Date().getSeconds(): new Date().getSeconds();
			return { 
				'year' : year , 
				'month' : month , 
				'date' : date , 
				'hh' : hh , 
				'mm' : mm , 
				'ss' : ss 
			} 
		},
		updateNow() { 
			this.nowTime = this.setDate().year + "-" + this.setDate().month + "-" + this.setDate().date + " " + this.setDate().hh + ":" + this.setDate().mm + ":" + this.setDate().ss;
		},
		fnc_router_go(location){
			this.$router.push(location);
		},
		LoginConfirm() {
			if(this.username == '' || this.password == ''){
				Swal.fire({
					title:'ID와 비밀번호를 입력해주시기 바랍니다.',
					icon: 'error'
				});
				return;
			}
		
			axios.post('/back/Login/Login', null, { params: {
				clm_user_id: this.username,
				clm_user_pw: this.password
			}})
			.then(response => {
				if (response.status == '200' && response.data.clm_user_id != '') {
					this.$session.start();
					this.$session.set('user_id', response.data.clm_user_id);
					this.$session.set('user_name', response.data.clm_user_name);
					this.$session.set('user_department', response.data.clm_user_department_id);
					this.$session.set('user_department_name', response.data.clm_user_department_name);
					this.$session.set('user_authority', response.data.clm_user_authority_id);
					this.$session.set('user_authority_name', response.data.clm_user_authority_name);
					this.$session.set('user_sabun', response.data.clm_user_sabun);
					location.reload();
				}else{
					Swal.fire({
						title:'ID와 패스워드를 확인해주시기 바랍니다.',
						icon: 'error'
					});
					this.$session.destroy();
				}
			})
			.catch(error => {
				console.log(error)
			})
		},
		LogOut() {
			Swal.fire({
				title:"로그아웃 하시겠습니까?",
				text:  '',
