			var index = this.tmp2_index ;
			if(this.productItems[index].clm_half_option_id=='0000' || this.productItems[index].clm_half_option_id=='' || typeof this.productItems[index].clm_half_option_id=='undefined'){
				let items = axios.get('/back/SCR003009/MaterialCubeTargetList',{
					params: {
