		async delPrdMat(index){
			if(this.productItems[index].clm_part_id != '' && this.productItems[index].clm_poor_yn == 'Y'){
				var exist_yn='N';
				for(var x=0;x<this.productItems.length;x++){
					if(this.productItems[index].clm_part_id==this.productItems[x].clm_part_id && x!=index &&  this.productItems[index].clm_poor_lotno==this.productItems[x].clm_poor_lotno && this.productItems[x].clm_material_lotno==''){
						exist_yn='Y';
					}
				}
				if(this.productItems[index].clm_same_count_yn == 'Y' && exist_yn=='N'){
					alert('부족수량이 존재하므로 삭제 할 수 없습니다.');
				}else{
					var tmp_material_order_use_total=0;
					for(var i=0;i<this.productItems.length;i++){
						if(this.productItems[i].clm_part_id==this.productItems[index].clm_part_id && this.productItems[i].clm_poor_yn=='Y' && this.productItems[index].clm_poor_lotno==this.productItems[i].clm_poor_lotno  ){
							tmp_material_order_use_total = Number(tmp_material_order_use_total) + Number(this.productItems[i].clm_use_count);
						}
					}
					for(var i=0;i<this.productItems.length;i++){
						if(this.productItems[i].clm_part_id==this.productItems[index].clm_part_id && this.productItems[i].clm_poor_yn=='Y' && this.productItems[index].clm_poor_lotno==this.productItems[i].clm_poor_lotno  ){
							this.productItems[i].clm_short_count=Number(this.productItems[i].clm_need_count)-tmp_material_order_use_total+Number(this.productItems[index].clm_use_count);
						}
					}
					this.productItems.splice(index,1);
				}
			}
		},
