						this.part_document_3[x].clm_check_yn='N';
					}
				}
			}
			for(var x=0;x<this.part_document_4.length;x++){
				if(this.part_document_4[x].clm_check_yn=='Y'){
					if(cnt!=4 || (cnt==4&& index!=x)){
						this.part_document_4[x].clm_check_yn='N';
					}
				}
