		async ProductSave(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			var Error_Mat_LotNo = 'N';
			var Error_Mat_Count = 'N';
			var Error_Mat_Count2 = 'N';
			var Error_Poor_Count = 'N';
			var Error_Poor_Count2 = 'N';
			var Error_Sum_less = 'N';
			var Error_Sum_over = 'N';
			var Error_Same_countYN = 'N';
			var materialData = '';
			var error_use='N';
			var cnt =0;
			var error_index= 0;
			if(this.productItems.length > 0){
				for(var i=0; i<this.productItems.length; i++){
					if(Number(this.productItems[i].clm_poor_count)>0 || this.productItems[i].clm_poor_yn=='Y'){
						var tmp_material_order_use_total=0;
						if(this.productItems[i].clm_poor_yn=='Y'||Number(this.productItems[i].clm_poor_count)>0 ){
							var tmp_material_cube_size =0;
							if(materialData != ''){
								materialData += '▒';
							}
							for(var j=0; j<this.productItems.length; j++){
								if(this.productItems[i].clm_part_id==this.productItems[j].clm_part_id && this.productItems[j].clm_poor_yn=='Y' && this.productItems[i].clm_poor_lotno==this.productItems[j].clm_poor_lotno){
									tmp_material_order_use_total = Number(tmp_material_order_use_total) + Number(this.productItems[j].clm_use_count);
								}
								if(this.productItems[i].clm_material_lotno== this.productItems[j].clm_material_lotno){
									tmp_material_cube_size = Number(tmp_material_cube_size) + Number(this.productItems[j].clm_use_count);
								}
							}
							if(this.productItems[i].clm_material_lotno == '' || this.productItems[i].clm_material_name == ''){
								Error_Mat_LotNo = 'Y';
								error_index=i;
							}
							if(this.productItems[i].clm_material_count == '' ){
								this.productItems[i].clm_material_count=0;
							}
							if(Number(tmp_material_order_use_total)!=0  && Number(tmp_material_order_use_total)!=Number(this.productItems[i].clm_need_count) && this.productItems[i].clm_same_count_yn=='Y' && Number(this.productItems[i].clm_poor_count)==0){
								// Error_Same_countYN = 'Y';
								alert(this.productItems[i].clm_part_name+'은(는) 필요수량과 사용수량이 동일해야합니다.');
								this.loading_yn='N';
								return;
							}
							if(Number(this.productItems[i].clm_use_count) < 0 || Number(this.productItems[i].clm_use_count)% 1!= 0){
								Error_Mat_Count = 'Y';
							}
							if(Number(this.productItems[i].clm_material_full_count) < Number(this.productItems[i].clm_use_count)){
								Error_Mat_Count2 = 'Y';
							}
							if(Number(this.productItems[i].clm_material_full_count) < Number(tmp_material_cube_size)){
								Error_Mat_Count2 = 'Y';
							}
							if(Number(this.productItems[i].clm_poor_count) < 0 || Number(this.productItems[i].clm_poor_count)% 1!= 0){
								Error_Poor_Count = 'Y';
							}
							if(Number(this.productItems[i].clm_material_full_count) < Number(this.productItems[i].clm_poor_count)){
								Error_Poor_Count2 = 'Y';
							}
							
							if(tmp_material_order_use_total > Number(this.productItems[i].clm_need_count)){
								Error_Sum_over = 'Y';
							}else if(tmp_material_order_use_total < Number(this.productItems[i].clm_need_count)){
								Error_Sum_less = 'Y';
							}
							//사용체크
							if(this.productItems[i].clm_material_id!='' && this.productItems[i].clm_poor_yn=='Y'){
								await axios.post('/back/Common/UseCheck', null, { params: {
								clm_joborder_id: this.clm_m2_joborder_id,
								clm_material_id: this.productItems[i].clm_material_id,
								clm_material_count: this.productItems[i].clm_material_full_count,
								clm_material_order_id:this.productItems[i].clm_material_order_id,
								clm_order_income_seq: this.productItems[i].clm_order_income_seq,
								clm_income_material_seq: this.productItems[i].clm_income_material_seq,
								clm_material_leftover_seq:this.productItems[i].clm_material_leftover_seq			
								}})
								.then(response => {
									if (response.status == '200') {
										if(response.data!='N'){
											cnt= i;
											error_use=response.data;
										}
									}
								})
								.catch(error => {
									console.log(error)
								})
							}
							materialData += this.productItems[i].clm_material_lotno + '▦' + this.productItems[i].clm_material_order_id + '▦' + this.productItems[i].clm_order_income_seq + '▦' + this.productItems[i].clm_income_material_seq + '▦' + this.productItems[i].clm_material_leftover_seq + '▦' + this.productItems[i].clm_material_id + '▦' + this.productItems[i].clm_material_full_count + '▦' + this.productItems[i].clm_use_count+ '▦' + this.productItems[i].clm_part_id + '▦' + tmp_material_cube_size+ '▦' + Number(this.productItems[i].clm_poor_count)+ '▦' + this.productItems[i].clm_poor_yn;
						}
					}
				}
			}	
			if(error_use=='Y'){
				alert(this.productItems[cnt].clm_material_lotno +'은(는) 이미 사용된 자재입니다.');
				this.loading_yn='N';
				return;
			}else if(error_use=='D'){
				alert(this.productItems[cnt].clm_material_lotno +'은(는) 삭제된 자재입니다.');
				this.loading_yn='N';
				return;
			}else if(error_use=='U'){
				alert(this.productItems[cnt].clm_material_lotno +'은(는) 수정된 자재입니다.');
				this.loading_yn='N';
				return;
			}
			if(Error_Mat_LotNo == 'Y'){
				alert(this.productItems[error_index].clm_part_name + '의 자재가 비어있습니다.');
				this.loading_yn='N';
				return;
			}
			if(Error_Mat_Count == 'Y'){
				alert('수량이 정상적이지 않은 자재가 있습니다.');
				this.loading_yn='N';
				return;
			}
			if(Error_Mat_Count2 == 'Y'){
				alert('자재가용수량을 초과한 자재가 있습니다.');
				this.loading_yn='N';
				return;
			}
			if(Error_Poor_Count == 'Y'){
				alert('불량수량이 정상적이지 않은 자재가 있습니다.');
				this.loading_yn='N';
				return;
			}
			if(Error_Poor_Count2 == 'Y'){
				alert('불량수량이 자재가용수량을 초과한 자재가 있습니다.');
				this.loading_yn='N';
				return;
			}
			var mapping_yn = 'N';
			var comment ='';
			if(Error_Sum_over == 'Y'){
				alert('필요수량보다 현재고수량이 많은 부품이 있습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m2_request_delivery_date != '' && this.clm_m2_request_delivery_time != '' && this.clm_m2_client_order_id!='CO000000'){
				this.clm_m2_request_delivery_datetime = this.clm_m2_request_delivery_date + ' ' + this.clm_m2_request_delivery_time+':00:00';
			}else{
				this.clm_m2_request_delivery_datetime = '';
			}
			if(this.clm_m2_request_delivery_datetime != this.clm_m_delivery_request_origin_datetime && Number(this.clm_m2_product_count) == Number(this.clm_m_product_origin_count)){
				comment = '배송요청일시변경('+this.clm_m_delivery_request_origin_datetime+'->'+this.clm_m2_request_delivery_datetime+')';
			}else if(this.clm_m2_request_delivery_datetime == this.clm_m_delivery_request_origin_datetime && Number(this.clm_m2_product_count) != Number(this.clm_m_product_origin_count)){
