			let items = axios.get('/back/Popup/StockAllPayment',{
				params: {
			        search: encodeURI(this.search)
					}
				}).then(
				response => {
					let items = response.data	
					vm.loading2 = false
					for(var x=0;x<items.length;x++ ) {
						if(x !=0 && items[x-1].clm_request_delivery_datetime == items[x].clm_request_delivery_datetime) { 
							// 이전 행의 데이터값과 기존 행의 데이터값이 같을 때 rowspan 값은 1이 되어야 함 (그래야 추가 병합이 일어나지 않음)
							items[x].rowspan = 'y'
						}
						if(x !=0 && items[x-1].clm_client_name == items[x].clm_client_name&& items[x-1].clm_request_delivery_datetime == items[x].clm_request_delivery_datetime) { 
							items[x].clientspan = 'y'
						}
						
					}
					this.m_payment_items = items
					console.log(items)
				}
			)
