				if(this.user_document_4[x].clm_check_yn=='Y'){
					id=id+this.user_document_4[x].clm_user_id+',';
					name=name+this.user_document_4[x].clm_user_name+',';
				}
			}
			
			if(id == '' || name ==''){
				alert('선택된 작업자가 없습니다.');
				return;
			}
			var count = id.split(',').length - 1;

			if(count==6){
				alert('작업자는 5명까지 등록가능합니다');
				return;
			}
			id = id.slice(0, -1);
			name =name.slice(0, -1);
			if(this.tmp_value=='After'){//후공정 작업자
