					response => {
						items = response.data
						for(var x=0;x<items.length;x++){
							console.log('items[x].search_id : '+items[x].search_id)
							if(items[x].search_id=='0005'){
								this.part_document_1.push(items[x])
							}
							if(items[x].search_id=='0007'){
								this.part_document_2.push(items[x])
							}
							if(items[x].search_id=='0009'){
								this.part_document_3.push(items[x])
							}
							if(items[x].search_id=='0004'){
								this.part_document_4.push(items[x])
							}
						}
					},
