		workEnd(){
			var error = 'N';
			this.loading_yn='N';
			var joborder_id = '';
			var product_id = '';
			var product_count = '';
			var repository_id = '';
			var repository_position_id = '';
			var client_order_id = '';

			if(this.selected_items.length == 1){
				if(this.selected_items[0].clm_assembly_end_datetime != ''){
					error = 'Y'
				}
				
				if(joborder_id == ''){
					joborder_id = this.selected_items[0].clm_joborder_id
				}else{
					joborder_id = joborder_id + ',' + this.selected_items[0].clm_joborder_id
				}

				if(product_id == ''){
					product_id = this.selected_items[0].clm_product_id
				}else{
					product_id = product_id + ',' + this.selected_items[0].clm_product_id
				}

				if(product_count == ''){
					product_count = String(this.selected_items[0].clm_product_count).replace(/,/gi, "")
				}else{
					product_count = product_count + ',' + String(this.selected_items[0].clm_product_count).replace(/,/gi, "")
				}

				if(client_order_id == ''){
					if( this.selected_items[0].clm_client_order_id =='재고용'){
						client_order_id='CO000000';
					}else{
						client_order_id = this.selected_items[0].clm_client_order_id;
					}
				}else{
					if( this.selected_items[0].clm_client_order_id =='재고용'){
						client_order_id='CO000000';
					}else{
						client_order_id = client_order_id + ',' + this.selected_items[0].clm_client_order_id
					}
				}

				if(repository_id == ''){
					repository_id = this.selected_items[0].clm_product_repository_location_id
				}else{
					repository_id = repository_id + ',' + this.selected_items[0].clm_product_repository_location_id
				}

				if(repository_position_id == ''){
					repository_position_id = this.selected_items[0].clm_product_repository_location_position_id
				}else{
					repository_position_id = repository_position_id + ',' + this.selected_items[0].clm_product_repository_location_position_id
				}
				

				if(error == 'Y'){
					alert('이미 작업완료된 조립작업이 선택되어 있습니다.');
					return;
				}else{
					this.m_part_user_name =this.selected_items[0].clm_assign_manager_name;
					this.m_client_name = this.selected_items[0].clm_client_name;
					this.m_delivery_request_datetime = this.selected_items[0].clm_delivery_request_datetime;
					this.m_product_name = this.selected_items[0].clm_product_name;
					this.m_product_count = String(this.selected_items[0].clm_product_count).replace(/,/gi, "");
					this.m_product_repository_location_name = this.selected_items[0].clm_product_repository_location_name;
					this.m_product_repository_location_position_name = this.selected_items[0].clm_product_repository_location_position_name;
					this.m_assemblylocationname = this.selected_items[0].clm_assembly_location_name;
					this.m_assemblyusername = this.selected_items[0].clm_assembly_user_name;
					this.m_assembly_start_datetime = this.selected_items[0].clm_assembly_start_datetime;
					if( this.selected_items[0].clm_client_order_id =='재고용'){
						this.m_client_order_id ='CO000000';
					}else{
						this.m_client_order_id = this.selected_items[0].clm_client_order_id;
					}
					this.m_joborder_id = this.selected_items[0].clm_joborder_id;
					this.m_joborder_user = this.selected_items[0].clm_assembly_user_id;
					this.m_joborder_user_name = this.selected_items[0].clm_assembly_user_name;
					this.Workitems=[{}];
					this.Workitems.splice(0, 1);
					axios.get('/back/MOB001004/SelectProductCompleteCount',{
						params: {
							client_order_id: this.m_client_order_id,
							joborder_id: this.m_joborder_id
						}
					})
					.then(response => {
						var usable_count = response.data
						if(usable_count == ''){
							usable_count = '0'
						}
						
						if((Number(this.m_product_count) - Number(usable_count)) > 0){
							this.m_product_complete_count = usable_count;
							this.m_product_usable_count = (Number(this.m_product_count) - Number(usable_count))
							this.m_product_produce_count = 0
						}else{
							this.m_product_complete_count = usable_count
							this.m_product_usable_count = 0
							this.m_product_produce_count = 0
						}
						let items = axios.get('/back/MOB001004/SelectComplete',{
						params: {
							client_order_id: this.m_client_order_id,
							joborder_id: this.m_joborder_id
							}
						}).then(
						response => {
							for(var x=0; x<response.data.length; x++){
								this.Workitems.push({
									clm_joborder_id :response.data[x].clm_joborder_id,
									clm_joborder_complete_seq : response.data[x].clm_joborder_complete_seq,
									clm_produce_count : Number(response.data[x].clm_produce_count),
									clm_complete_user : response.data[x].clm_user_name,
									clm_produce_finish_datetime : response.data[x].clm_produce_finish_datetime
								});
							}
						})
					});
					this.WorkEndModal = true
				}
			}else if(this.selected_items.length == 0){
				alert('선택된 작업이 없습니다.')
				return
			}else{
				alert('1개의 작업만 선택이 가능합니다.')
				return
			}
